<template>
    <div>
        <v-data-table
                :headers="headers"
                :items="desserts"
                sort-by="calories"
                class="elevation-1"
        >
            <template v-slot:item.status="{ item }">
                <div>
                    <v-chip
                            small
                            label
                            color="green"
                            class="white--text"
                    >
                        Обработан
                    </v-chip>
                </div>
            </template>
            <template v-slot:item.actions="{ item }">
                <v-btn
                        x-small
                        class="mr-2 white--text"
                        @click="confirm = true"
                        fab
                        color="primary"
                >
                    <v-icon>mdi-content-save</v-icon>
                </v-btn>
                <v-btn
                        x-small
                        class="mr-2 white--text"
                        @click="confirm = true"
                        fab
                        color="green"
                >
                    <v-icon>mdi-check</v-icon>
                </v-btn>
                <v-btn
                        x-small
                        class="mr-2"
                        @click="confirm = true"
                        fab
                        color="secondary"
                >
                    <v-icon>mdi-backup-restore</v-icon>
                </v-btn>
                <v-btn
                        x-small
                        class="mr-2 white--text"
                        @click="editItem(item)"
                        fab
                        color="blue accent-2"
                >
                    <v-icon>mdi-pencil</v-icon>
                </v-btn>
                <v-btn
                        x-small
                        @click="deleteItem(item)"
                        fab
                        color="red"
                        class="white--text"
                >
                    <v-icon>mdi-delete</v-icon>
                </v-btn>
            </template>
            <template v-slot:no-data>
                <v-btn
                        color="primary"
                        @click="initialize"
                >
                    Reset
                </v-btn>
            </template>
        </v-data-table>

        <v-dialog
                v-model="dialog"
                max-width="500px"
        >
            <v-card>
                <v-card-title>
                    <span class="headline">{{ formTitle }}</span>
                </v-card-title>

                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col
                                    cols="12"
                                    sm="6"
                            >
                                <v-text-field
                                        v-model="editedItem.id"
                                        label="№"
                                ></v-text-field>
                            </v-col>
                            <v-col
                                    cols="12"
                                    sm="6"
                            >
                                <v-text-field
                                        v-model="editedItem.date"
                                        label="Дата"
                                ></v-text-field>
                            </v-col>
                            <v-col
                                    cols="12"
                                    sm="6"
                            >
                                <v-text-field
                                        v-model="editedItem.contragent"
                                        label="Поставщик"
                                ></v-text-field>
                            </v-col>
                            <v-col
                                    cols="12"
                                    sm="6"
                            >
                                <v-text-field
                                        v-model="editedItem.place"
                                        label="Торговая точка"
                                ></v-text-field>
                            </v-col>
                            <v-col
                                    cols="12"
                                    sm="6"
                            >
                                <v-text-field
                                        v-model="editedItem.face"
                                        label="Ответсвенное лицо"
                                ></v-text-field>
                            </v-col>
                            <v-col
                                    cols="12"
                                    sm="6"
                            >
                                <v-text-field
                                        v-model="editedItem.number"
                                        label="Порядковый номер товара"
                                ></v-text-field>
                            </v-col>
                            <v-col
                                    cols="12"
                                    sm="6"
                            >
                                <v-text-field
                                        v-model="editedItem.code"
                                        label="Код"
                                ></v-text-field>
                            </v-col>
                            <v-col
                                    cols="12"
                                    sm="6"
                            >
                                <v-text-field
                                        v-model="editedItem.name"
                                        label="Наименование товара"
                                ></v-text-field>
                            </v-col>
                            <v-col
                                    cols="12"
                                    sm="6"
                            >
                                <v-text-field
                                        v-model="editedItem.unit"
                                        label="Ед. измерения"
                                ></v-text-field>
                            </v-col>
                            <v-col
                                    cols="12"
                                    sm="6"
                            >
                                <v-text-field
                                        v-model="editedItem.count"
                                        label="Количество"
                                ></v-text-field>
                            </v-col>

                            <v-col
                                    cols="12"
                                    sm="6"
                            >
                                <v-text-field
                                        v-model="editedItem.price"
                                        label="Цена"
                                ></v-text-field>
                            </v-col>
                            <v-col
                                    cols="12"
                                    sm="6"
                            >
                                <v-text-field
                                        v-model="editedItem.sum"
                                        label="Сумма"
                                ></v-text-field>
                            </v-col>
                        </v-row>
                    </v-container>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                            color="dark darken-1"
                            text
                            @click="close"
                    >
                        Отмена
                    </v-btn>
                    <v-btn
                            color="green darken-1"
                            text
                            @click="save"
                    >
                        Сохранить
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <br><br>
        <v-btn
                color="green"
                fab
                class="btn-plus white--text"
                @click="editItem()"
        >
            <v-icon>mdi-plus</v-icon>
        </v-btn>


        <v-dialog
                v-model="confirm"
                max-width="290"
        >
            <v-card>
                <v-card-title class="headline justify-center">
                    Подтвердите
                </v-card-title>

                <v-card-actions class="justify-center">
                    <v-btn
                            color="red darken-1"
                            text
                            @click="confirm = false"
                    >
                        отмена
                    </v-btn>

                    <v-btn
                            color="green darken-1"
                            text
                            @click="confirm = false"
                    >
                        подтвердить
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>


<style lang="scss" scoped>

</style>
<script>
    export default {
        data: () => ({
            dialog: false,
            confirm: false,
            headers: [
                {text: '№', align: 'start', value: 'id'},
                {text: 'Дата', value: 'date'},
                {text: 'Поставщик', value: 'contragent'},
                {text: 'Торговая точка', value: 'place'},
                {text: 'Ответсвенное лицо', value: 'face'},
                {text: 'Порядковый номер товара', value: 'number'},
                {text: 'Код', value: 'code'},
                {text: 'Наименование товара', value: 'name'},
                {text: 'Ед. измерения', value: 'unit'},
                {text: 'Количество', value: 'count'},
                {text: 'Цена', value: 'price'},
                {text: 'Сумма', value: 'sum'},
                {text: 'Статус', align: 'center', value: 'status'},
                {text: '', value: 'actions'}
            ],
            desserts: [],
            editedIndex: -1,
            editedItem: {},
            defaultItem: {
                id: '',
                date: new Date().toLocaleDateString(),
                contragent: '',
                place: '',
                face: '',
                number: '',
                code: '',
                name: '',
                unit: '',
                count: '',
                price: '',
                sum: '',
                status: '',
            },
        }),

        computed: {
            formTitle() {
                return this.editedIndex === -1 ? 'Добавить' : 'Изменить'
            },
        },

        watch: {
            dialog(val) {
                val || this.close()
            },
        },

        created() {
            this.initialize()
        },

        methods: {
            initialize() {
                this.desserts = [
                    {
                        id: '1',
                        date: new Date().toLocaleDateString(),
                        contragent: 'Частное лицо',
                        place: 'Торговая точка 1',
                        face: 'Хайруллин Тимур',
                        number: '00001',
                        code: '00001',
                        name: 'Товар 1',
                        unit: 'Кг',
                        count: '20',
                        price: '500 ₸',
                        sum: '10000 ₸',
                        status: 'Обработан',
                        actions: ''
                    },
                    {
                        id: '2',
                        date: new Date().toLocaleDateString(),
                        contragent: 'Частное лицо',
                        place: 'Торговая точка 1',
                        face: 'Хайруллин Тимур',
                        number: '00001',
                        code: '00001',
                        name: 'Товар 1',
                        unit: 'Кг',
                        count: '20',
                        price: '500 ₸',
                        sum: '10000 ₸',
                        status: 'Сохранен',
                        actions: ''
                    }
                ]
            },

            editItem(item) {
                this.editedIndex = this.desserts.indexOf(item)
                this.editedItem = Object.assign({}, item)
                this.dialog = true
            },

            deleteItem(item) {
                const index = this.desserts.indexOf(item)
                confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1)
            },

            close() {
                this.dialog = false
                this.$nextTick(() => {
                    this.editedItem = Object.assign({}, this.defaultItem)
                    this.editedIndex = -1
                })
            },

            save() {
                if (this.editedIndex > -1) {
                    Object.assign(this.desserts[this.editedIndex], this.editedItem)
                } else {
                    this.desserts.push(this.editedItem)
                }
                this.close()
            },
        },
    }
</script>

<style lang="scss" scoped>
    .v-text-field {
        margin: 0;
        padding: 0;
    }

    .btn-plus {
        position: fixed;
        bottom: 70px;
        right: 20px;
    }
</style>
